#contenedorpadre {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    width: 70%;
    padding: 3% 0;
}

#contenedorformu {
    margin: auto;
    width: 34%;
    border-top: 1px solid rgba(102, 102, 102, 0.102);
    border-bottom: 1px solid rgba(122, 122, 122, 0.128);
    padding: 1%;
}

.divimagen {
    width: 30%;
}

img {
    width: 100%;
    height: 100%;
}

@media (max-width: 900px) {

    #contenedorformu {
        width: 300px;
        padding: 0%;
    }

    .divimagen {
        display: none;
    }

    #contenedorformu {
        border: none;
    }
}


